
export default {
    name: 'QuizList',
    data() {
        return {
            quizes: [],
        }
    },
    methods: {
        async getData() {
            const response = await this.$http.get(
                "http://127.0.0.1:8000/api/articless/",
            );

            this.quizes = response.data
        }, catch (error) {
            console.log(error)
        },
    },
    created() {
    this.getData();
  },
}

